*,
*::after,
*::before {
    box-sizing: border-box;
}

.app-container {
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 600px;
}

.todoList {
    padding: 0;
    margin: 10px 0 0 0;
    width: 100%;
    font-size: 1.5rem;
    border-radius: 15px;
}

.todoList li {
    border-top-width: unset !important;
    border-radius: 15px;
    background-color: gainsboro;
}

.todoList li:not(:last-child) {
    margin-bottom: 0.35rem;
}

.list-item {
    padding: 5px 20px;
}
.list-item .li-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.list-item:hover {
    cursor: pointer;
}
.list-item button {
    width: 43px;
}

.list-item .importantBtn {
    background-color: orange;
}

.list-item .importantBtn:hover {
    background-color: yellow;
}

.list-item .deleteBtn {
    background-color: rgb(245, 110, 110);
}

.list-item .deleteBtn:hover {
    background-color: tomato;
}

.timestamp {
    opacity: 70%;
    font-size: 1rem;
}
